.chart-cx {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.chart-cx-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 35px;
}

.chart-cx-header h3 {
  margin: 0;
	font-weight: 700;
	font-size: 22px;
	line-height: 150%;
}

.small-msg {
  color: #616163;
  font-weight: 500;
  font-size: 14px;
	line-height: 20px;
}

.full-chart-cx {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
	padding: 32px 35px 0;
}

.top-border {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 32px;
}

.card-in-chart-cx {
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: space-between;

  h3 {
    font-family: "Arimo", sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    margin: 0;
  }
}

.single-card-in-chart-cx-wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.single-card-first-in-chart-cx {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.single-card-first-in-chart-cx > p {
  color: rgba(0, 0, 0, 0.7);
  font-family: "Arimo", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  margin: 0;
}

.info-icon-wrap-cx {
  display: flex;
  align-items: center;
  gap: 7px;

  img {
    width: 24px;
  }

  p {
    font-family: "Arimo", sans-serif;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    margin: 0;
  }
}

.full-chart-area-cx {
  margin: 10px 0 0 0;
}

