.add-campaign-card {
  /* width: 100%; */
  display: flex;
  flex-direction: column;
  padding: 32px 25px 0;
	margin: 0 10px 0;
	border-top: 1px solid rgba(0, 0, 0, .2);
}

/* .add-campaign-card div {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
} */

.add-campaign-form-fields {
	display: grid;
	gap: 20px;
	grid-template-columns: repeat(2, minmax(0, 1fr));
}

.add-campaign-form-fields-item:first-of-type {
	grid-column-start: 1;
}

.add-campaign-form-fields-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.add-campaign-form-fields-item img {
  width: 32px;
}

.add-campaign-form-fields-item input {
  height: 52px;
  border-radius: 8px;
  font-size: 16px;
  padding: 14px 16px;
  border: 1px solid #000000;
  outline: none;
	width: 100%;
}

.add-campaign-card select {
	grid-column-start: 1;
	grid-column: span 1 / span 1;
  height: 52px;
  padding: 14px 16px;
  border-radius: 8px;
  border: 1px solid #000000;
  font-size: 16px;
}

.add-campaign-card select:hover {
  border: 1px solid var(--primary-color);
}

.add-campaign-form-fields .add-campaign-form-button-wrap {
	grid-column: span 1 / span 1;
}

.add-campaign-form-fields button {
  height: 48px;
  min-width: 100px;
  padding: 12px 24px;
  border-radius: 9999px;
  border: none;
  font-family: "Arimo", sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: #ffffff;
  background: var(--primary-color);
  cursor: pointer;
}

.add-campaign-card > h3 {
  margin: 0;
	font-weight: 700;
	font-size: 22px;
	line-height: 150%;
	margin-bottom: 24px;
}

.add-campaign-card small {
  font-size: 14px;
  font-style: italic;
}

.add-campaign-tags {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 8px !important;
}

.add-campaign-tags > div {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 16px;
}

.add-campaign-tags input {
  height: 32px;
  border-radius: 12px;
  font-size: 16px;
  padding: 0px 10px;
  border: 1px solid #000000;
  outline: none;
}

.add-campaign-tags input:focus {
  border: 1px solid var(--primary-color);
}

.tags-section {
	grid-column: span 2 / span 2;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.single-tag {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  background-color: #e0e0e0;
}

.add-campaign-fields {
  display: grid !important;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.add-campaign-fields input {
  grid-column-start: 1;
  grid-column-end: 2;
  height: 32px;
  border-radius: 12px;
  font-size: 16px;
  padding: 0px 10px;
  border: 1px solid #000000;
  outline: none;
}

.add-campaign-fields input:focus {
  border: 1px solid var(--primary-color);
}
