.add-post-container {
  margin-bottom: 24px;
  padding: 0 35px;
}

.add-post-container h3 {
  margin: 0;
  font-weight: 700;
  font-size: 22px;
  line-height: 150%;
  margin-bottom: 24px;
}

.form-container {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  align-items: center;
  gap: 8px;
}

.campaign-options {
  grid-column: span 5 / span 5;
}

.post-list-title {
  font-weight: 500;
  font-size: 18px;
  grid-column: span 5 / span 5;
  margin-bottom: 12px;
}

.campaign-options select {
  width: 50%;
  height: 52px;
  padding: 14px 16px;
  border-radius: 8px;
  border: 1px solid #000000;
  font-size: 16px;
  margin-bottom: 24px;
}

.campaign-options select:hover {
  border: 1px solid var(--primary-color);
}

.post-table {
  width: 100%;
  grid-column: span 5 / span 5;
  border-collapse: collapse;
}

.post-table tr {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.post-table-head th {
  text-align: left;
  font-size: 16px;
  font-weight: 500;
  padding: 8px;
}

.post-table td {
  padding: 16px 8px;
}

.post-detail {
  display: flex;
  align-items: center;
  gap: 16px;
}

.post-detail img {
  height: 56px;
  width: 56px;
  object-fit: cover;
  border-radius: 9999px;
}

.post-link {
  font-size: 16px;
  color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  gap: 4px;
}

.post-button {
  cursor: pointer;
  background: transparent;
  color: var(--primary-color);
  border: none;
}
