.access-guard-container {
    width: 100%;
    margin: 48px auto 32px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.access-guard-container > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    min-width: 350px;
    min-height: 200px;
    box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
    padding: 24px;
    border-radius: 24px;
}

.access-guard-container > div h1 {
    font-size: 20px;
    margin-bottom: 0;
}

.access-guard-container > div p {
    margin: 8px 0 6px;
    font-size: 14px;
}

.access-guard-container > div button {
    height: 48px;
    padding: 12px 24px;
    width: 100%;
    border-radius: 9999px;
    border: none;
    font-family: "Arimo", sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: #ffffff;
    background: var(--primary-color);
    cursor: pointer;				
    margin-top: 48px;
}