.top-posts-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 24px;
}

.top-posts-header h3 {
  margin: 0;
	font-weight: 700;
	font-size: 22px;
	line-height: 150%;
}

.top-posts-box {
	padding: 0 35px;
}

.small-msg {
  color: #616163;
  font-weight: 500;
  font-size: 14px;
	line-height: 20px;
}

.post-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 15px;

  .post-item {
    cursor: pointer;
    padding: 25px 20px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    gap: 23px;

    p {
      margin: 0;
    }

    .post-item-header {
      font-family: "Arimo", sans-serif;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 0.5px;    
      color: rgba(0, 0, 0, 0.5);
      display: flex;
      align-items: center;
      gap: 8px;
    }

    .post-item-header p {
      margin: 0;
    }

    .post-item-header img {
      width: 32px;
      object-fit: cover;
      border-radius: 9999px;
    }

    .post-item-img {
      img {
        width: 100%;
        object-fit: cover;
        border-radius: 8px;
        aspect-ratio: 1 / 1;
      }
    }

    .post-item-desc {
      width: 100%;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
      letter-spacing: 0.5px;
      color: rgba(0, 0, 0, 0.7);
    }

    .post-item-metrics {
      display: flex;
      align-items: center;
      gap: 15px;
      margin-top: auto;
      flex-wrap: wrap;
    }

    .post-item-metrics div {
      display: flex;
      align-items: center;
      gap: 5px;
    }

    .post-item-metrics img {
      width: 20px;
    }

    .post-item-metrics p {
      font-size: 12px;
      font-weight: 600;
      color: #000000;
    }
  }
}
