.alert-container {
  padding: 0 35px;
}

.alert {
  border: 1px solid #e32662;
  border-radius: 6px;
  padding: 16px;
  display: flex;
  gap: 0.75rem;
}

.alert-icon {
  width: 1rem;
  height: 1rem;
  color: #e32662;
  flex-shrink: 0;
}

.alert-content {
  flex: 1;
}

.alert-title {
  color: #e32662;
  font-weight: 600;
  font-size: 1rem;
  margin: 0 0 0.5rem 0;
}

.toggle-button {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
  background: none;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.toggle-button:hover {
  background-color: rgba(220, 38, 38, 0.1);
}

.error-count {
  font-weight: 500;
}

.chevron-icon {
  width: 1rem;
  height: 1rem;
}

.error-list {
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.error-item {
  background-color: #fff1f2;
  padding: 0.75rem;
  border-radius: 4px;
}

.error-url {
  font-size: 16px;
  color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  gap: 4px;
}

.error-message {
  color: #e32662;
  font-size: 0.875rem;
  margin: 0.25rem 0 0 0;
}
