.header-cont {
  margin: 32px auto 0;
  padding: 0 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 24px;
}

.header-cont img {
  max-width: 167px;
}

.header-cont p {
  margin: 0px;
  text-align: center;
  font-family: "Arimo", sans-serif;
  font-size: 18px;
  line-height: 24px;
}
