.invalid-cont {
    margin: 0px auto 16px auto;
    padding: 0px 32px;
    width: 400px;
    display: flex;
    flex-direction: column;
}

.invalid-cont span {
    text-align: center;
    width: fit-content;
    background-color: #f8d7da;
    color: #721c24;
    border: 1px solid #721c24;
    border-radius: 8px;
    padding: 8px 12px;
    font-size: 14px;
    font-weight: 500;
    margin: 16px 0px;
}

.invalid-cont p {
    margin: 8px 0px;
}