.main-cont {
	width: 780px;
  margin: 48px auto 0;
  padding-bottom: 35px;
  font-family: "Arimo", sans-serif;
}

.top-card {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 24px;
  padding: 0 35px;
}

.button-box a {
  height: 48px;
  width: 100%;
  padding: 12px 24px;
  border-radius: 9999px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  cursor: pointer;
  text-decoration: none;
}

.left-box {
  display: flex;
  flex-direction: column;
}

.left-box > button {
  margin: 12px 0px;
  width: 40%;
  height: 42px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  font-weight: 600;
  color: #ffffff;
  background: #e32662;
  cursor: pointer;
}

.input-cont {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.input-cont p {
  margin: 0px 0px 10px 0px;
  font-size: 14px;
}

.input-cont small {
  color: red;
  margin-top: 8px;
  font-size: 12px;
  width: 85%;
}

.input-cont input {
  width: 85%;
  height: 42px;
  outline: none;
  border: 1px solid #ced4da;
  border-radius: 8px;
  padding-left: 10px;
  font-size: 14px;
}

.input-cont input:focus {
  border: 1px solid #e32662;
}

.radio-cont {
  margin: 16px 0px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.radio-cont label {
  font-size: 14px;
}

.right-box {
  display: flex;
  flex-direction: column;
}

.info-cx {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.info-cx span {
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
  border: 1px solid;
  border: 1px solid #ced4da;
}

.info-cx span h2 {
  margin: 0px;
  font-size: 14px;
}

.info-cx span p {
  margin: 0;
  font-size: 16px;
}

.right-box button {
  width: 85%;
  margin: 0px auto;
}

.error-box {
  width: fit-content;
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #721c24;
  border-radius: 8px;
  padding: 8px 12px;
  font-size: 14px;
  font-weight: 500;
  margin: 16px 0px;
}

.bottom-card {
	margin: 32px auto 0;
}

.middle-card {
	margin: 32px auto 0;
}

.last-card {
	margin: 32px auto 0;
}

.refresh-cta {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.refresh-cta > span {
  font-size: 14px;
  color: var(--primary-color);
  font-weight: 500;
}

.button-box {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.button-box button {
  height: 48px;
  width: 100%;
  padding: 12px 24px;
  border-radius: 9999px;
  border: none;
  font-family: "Arimo", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: #ffffff;
  background: var(--primary-color);
  cursor: pointer;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.button-box a {
  width: 100%;
}

.url-cx {
  margin-bottom: 16px;
}

.url-cx h3 {
  font-size: 14px;
  margin: 0px 0px 4px 0px;
  font-weight: 500;
}

.url-cx p {
  font-size: 16px;
  margin: 4px 0px;
}

.url-cx a {
  border: none;
  height: auto;
  cursor: pointer;
  display: block;
  font-size: 16px;
  margin: 4px 0px;
  font-weight: 400;
  color: #0000ee;
}

.url-cx a:hover {
  text-decoration: underline;
}

.cta-button-cx {
  width: 100%;
  justify-content: space-between;
  display: flex;
  gap: 8px;
  margin-top: 16px;
}

.tabs-section {
  width: 100%;
	border-top: 1px solid rgba(0, 0, 0, .2);
	border-bottom: 1px solid rgba(0, 0, 0, .2);
  margin-top: 32px;
  
  .tabs-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding: 8px;

    button {
      background: none;
      border: none;
      font-family: "Arimo", sans-serif;
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
      letter-spacing: 0.5px;
      padding: 12px 20px;
      cursor: pointer;
    }
  }

}

.advanced-metrics-section {
	margin: 32px auto 0;
}

.top-posts-section {
	margin: 32px auto 0;
}
