.bulk-influencer {
  display: flex;
  flex-direction: column;
  padding: 32px 25px 0;
  margin: 0 10px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.2);

	h3 {
		margin: 0;
		font-weight: 700;
		font-size: 22px;
		line-height: 150%;
		margin-bottom: 24px;
	}

	.bulk-influencer-form-fields {
		display: grid;
		gap: 20px;
		grid-template-columns: repeat(2, minmax(0, 1fr));

		.bulk-influencer-form-fields-item {
			display: flex;
			align-items: center;
			gap: 8px;
		}

		.bulk-influencer-form-fields-item img {
			width: 32px;
		}

		.bulk-influencer-form-fields-item input {
			height: 52px;
			border-radius: 8px;
			font-size: 16px;
			padding: 14px 16px;
			border: 1px solid #000000;
			outline: none;
			width: 100%;
		}	
	}

	.tags-section {
		grid-column: span 2 / span 2;
		display: flex;
		flex-wrap: wrap;
		gap: 8px;

		.single-tag {
			display: flex;
			align-items: center;
			gap: 4px;
			padding: 4px 8px;
			border-radius: 12px;
			font-size: 12px;
			background-color: #e0e0e0;
		}		
	}	

	.bulk-influencer-form-button-wrap {
		grid-column: span 1 / span 1;

		button {
			height: 48px;
			min-width: 100px;
			padding: 12px 24px;
			border-radius: 9999px;
			border: none;
			font-family: "Arimo", sans-serif;
			font-size: 12px;
			font-weight: 600;
			line-height: 24px;
			letter-spacing: 0.5px;
			color: #ffffff;
			background: var(--primary-color);
			cursor: pointer;				
		}
	}	
}
