.metrics-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 24px;
}

.metrics-header h3 {
  margin: 0;
	font-weight: 700;
	font-size: 22px;
	line-height: 150%;
}

.metrics-box {
	padding: 0 35px;
}

.small-msg {
  color: #616163;
  font-weight: 500;
  font-size: 14px;
	line-height: 20px;
}

.metrics-cx {
  display: grid;
	grid-template-columns: repeat(3, minmax(0, 1fr));
  width: 100%;
  column-gap: 20px;
	row-gap: 48px;
}

/* .section-cx {
  display: flex;
  gap: 12px;
  width: 100%;
} */

.block-cx {
  display: flex;
  flex-direction: column;
	gap: 8px;
  font-family: "Arimo", sans-serif;
}

.block-cx .metrics-item-top {
	display: flex;
	align-items: center;
	gap: 8px;
}

.block-cx .metrics-item-top img {
	width: 22px;
}

.block-cx .metrics-item-top p {
	font-size: 26px;
	font-weight: 600;
	line-height: 32px;
	margin: 0;
	color: #000;
}

.block-cx > p {
	margin: 0;
	line-height: 24px;
	letter-spacing: 0.5px;
	font-size: 16px;
  color: #616163;
}

.block-cx .metrics-item-badge {
  font-size: 14px;
  font-weight: 600;
	margin: 0;
	line-height: 20px;
	padding: 4px 8px;
	border-radius: 9999px;
	align-self: flex-start
}
